/* eslint-disable */



export const displayMap = (locations) => {
  mapboxgl.accessToken =
    'pk.eyJ1IjoiYmpvcm50aXJzZW4iLCJhIjoiY2txbDV2cGYwMGE5eDJvcGQ3MGJyZ2MwOCJ9.-lVt6hc1YtdQ9nQYQgh4FQ';

  var map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/bjorntirsen/ckql6cie107ct17mop18zomqm',
    scrollZoom: false,
    /* center: [18.075604, 59.31451],
  zoom: 10,
  interactive: false, */
  });

  const bounds = new mapboxgl.LngLatBounds();

  locations.forEach((loc) => {
    // Create marker
    const el = document.createElement('div');
    el.className = 'marker';
    // Add marker
    new mapboxgl.Marker({
      element: el,
      anchor: 'bottom',
    })
      .setLngLat(loc.coordinates)
      .addTo(map);

    // Add popup
    new mapboxgl.Popup({
      offset: 30,
    })
      .setLngLat(loc.coordinates)
      .setHTML(`<p>Day ${loc.day}: ${loc.description}`)
      .addTo(map);

    //extend map bounds to include locations
    bounds.extend(loc.coordinates);
  });

  map.fitBounds(bounds, {
    padding: {
      top: 200,
      bottom: 150,
      left: 100,
      right: 200,
    },
  });
};
